<template>
    <v-container :class="{'h-full': booksItem === undefined }">
        <v-row v-if="booksItem !== undefined">
            <v-col cols="12">
                <v-card>
                    <v-container>
                        <v-row dense>
                            <v-col 
                                cols="5"
                                sm="2"
                                lg="1"
                                class="d-sm-flex align-sm-center"
                            >
                                <v-card 
                                    class="pa-3 h-full"
                                >
                                    <v-img
                                        :src="booksItem.cover"
                                        contain
                                        class="h-full"
                                    >
                                    </v-img>
                                </v-card>
                            </v-col>
                            <v-col 
                                cols="7"
                                sm="10"
                                lg="11"
                                class="d-md-flex flex-md-column"
                            >
                                <v-card class="flex-md-grow-1">
                                    <v-card-title class="pa-2 pa-sm-4">{{ booksItem.title }}</v-card-title>
                                    <v-card-subtitle class="pt-2 pt-sm-1">- {{booksItem.author}}</v-card-subtitle>
                                    <v-divider class="d-sm-none" />
                                    <div class="d-flex pl-3">
                                        <div class="d-none d-sm-flex flex-sm-grow-1">
                                            <div class="flex-sm-grow-1 align-self-center">
                                                <v-tooltip 
                                                    bottom
                                                    v-for="genre in booksItem.genre"
                                                    :key="genre"
                                                >
                                                    <template v-slot:activator="{ on, attrs }">
                                                        <v-icon
                                                            v-bind="attrs"
                                                            v-on="on"
                                                        >
                                                            mdi-{{genre.icon}}
                                                        </v-icon>
                                                    </template>
                                                    <span>{{genre.text}}</span>
                                                </v-tooltip>
                                            </div>
                                            <v-chip-group>
                                                <v-chip 
                                                    v-for="copy in checkCurentCopy"
                                                    :key="copy"
                                                    color="info"
                                                >
                                                    {{copy}}
                                                </v-chip>
                                            </v-chip-group>                                  
                                        </div>
                                        <div class="d-flex d-sm-none">
                                            <v-tooltip 
                                                bottom
                                                v-for="genre in booksItem.genre"
                                                :key="genre"
                                            >
                                                <template v-slot:activator="{ on, attrs }">
                                                    <v-icon
                                                        v-bind="attrs"
                                                        v-on="on"
                                                    >
                                                        mdi-{{genre.icon}}
                                                    </v-icon>
                                                </template>
                                                <span>{{genre.text}}</span>
                                            </v-tooltip>
                                        </div>
                                        <v-btn
                                            icon
                                            @click="isOpenMainDesctiption = !isOpenMainDesctiption"
                                            class="d-flex d-sm-none ml-auto"
                                        >
                                            <v-icon>mdi-chevron-{{ isOpenMainDesctiption ? "up" : "down" }}</v-icon>
                                        </v-btn>
                                    </div>
                                    <v-expand-transition appear>
                                        <div v-show="isOpenMainDesctiption || $vuetify.breakpoint.smAndUp">
                                            <v-divider />
                                            <v-card-actions class="pa-sm-0">
                                                <v-container 
                                                    fluid
                                                    class="pa-0"
                                                >
                                                    <v-row 
                                                        dense 
                                                        no-gutters
                                                        class="px-sm-2 d-flex d-sm-none"
                                                    >
                                                        <v-col
                                                            cols="12" 
                                                            sm="6"
                                                            class="d-sm-flex justify-sm-end"
                                                        >
                                                            <v-chip-group>
                                                                <v-chip 
                                                                    v-for="copy in checkCurentCopy"
                                                                    :key="copy"
                                                                    color="info"
                                                                >
                                                                    {{ copy }}
                                                                </v-chip>
                                                            </v-chip-group>
                                                        </v-col>
                                                    </v-row>
                                                    <v-row 
                                                        dense 
                                                        no-gutters
                                                    >
                                                        <v-col 
                                                            v-show="$vuetify.breakpoint.smAndUp"
                                                            class="d-none d-sm-block"
                                                            cols="12"
                                                        >
                                                            <div class="d-sm-flex">
                                                                <v-rating
                                                                    half-increments
                                                                    dense
                                                                    :value="booksItem.rating"
                                                                    class="flex-sm-grow-1 align-self-center pl-sm-2"
                                                                />
                                                                <v-btn color="success">
                                                                    {{booksItem.price}}
                                                                    <v-icon 
                                                                        right
                                                                    >
                                                                        mdi-currency-rub
                                                                    </v-icon>
                                                                </v-btn>
                                                            </div>
                                                        </v-col>
                                                    </v-row>
                                                </v-container>
                                            </v-card-actions>
                                        </div>
                                    </v-expand-transition>
                                </v-card>
                            </v-col>
                            <v-col 
                                cols="12"
                            >
                                <v-card>
                                    <div class="d-flex align-center">
                                        <v-card-subtitle>Отзывы</v-card-subtitle>
                                        <v-btn
                                            icon
                                            @click="isOpenReviews = !isOpenReviews"
                                            class="ml-auto"
                                        >
                                            <v-icon>mdi-chevron-{{ isOpenReviews ? "up" : "down" }}</v-icon>
                                        </v-btn>
                                    </div>
                                    <v-expand-transition appear>
                                        <div v-show="isOpenReviews">
                                            <v-divider />
                                            <v-list three-line>
                                                <template
                                                    v-for="(item, i) in testReviews"
                                                >
                                                    <v-divider :key="i" v-if="item.divider" />
                                                    <v-list-item 
                                                        :key="item.name"
                                                        v-else
                                                    >
                                                        <v-list-item-avatar>
                                                            <v-icon>mdi-{{ item.avatar }}</v-icon>
                                                        </v-list-item-avatar>
                                                        <v-list-item-content>
                                                            <v-list-item-title>{{ item.name }}</v-list-item-title>
                                                            <v-list-item-subtitle>{{ item.text }}</v-list-item-subtitle>
                                                        </v-list-item-content>
                                                    </v-list-item>
                                                </template>
                                            </v-list>
                                        </div>
                                    </v-expand-transition>
                                </v-card>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col 
                                cols="12"
                            >
                                <v-btn
                                    tile
                                    block
                                    @click="isOpenDescriptionCard = !isOpenDescriptionCard"
                                >
                                    <p class="mb-0 flex-grow-1">{{isOpenDescriptionCard ? "Закрыть" : "Открыть"}} описание</p>
                                    <v-icon>mdi-chevron-{{ isOpenDescriptionCard ? "up" : "down" }}</v-icon>
                                </v-btn>
                                <v-expand-transition>
                                    <v-card 
                                        v-show="isOpenDescriptionCard"
                                        tile
                                    >   
                                        <v-card-title>Описание</v-card-title>
                                        <v-divider />
                                        <v-card-actions>
                                            <v-card-text>{{booksItem.description}}</v-card-text>
                                        </v-card-actions>
                                    </v-card>
                                </v-expand-transition>
                            </v-col>
                        </v-row>
                        <v-row class="d-flex d-sm-none">
                            <v-divider />
                            <v-col cols="12">
                                <v-card>
                                    <v-card-actions>
                                        <v-rating
                                            half-increments
                                            dense
                                            :value="booksItem.rating"
                                            class="flex-grow-1"
                                        />
                                        <v-btn color="success">
                                            {{booksItem.price}}
                                            <v-icon 
                                                right
                                            >
                                                mdi-currency-rub
                                            </v-icon>
                                        </v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>
        <v-row 
            v-else
            class="h-full"
            align="center"
        >
            <v-col cols="12">
                <v-card>
                    <v-container fluid>
                        <v-row align="center">
                            <v-col 
                                cols="6"
                                sm="8"
                            >
                                <v-card-subtitle class="d-flex">
                                    <p class="mb-0 text-non-space">Не найдено</p>
                                    <v-icon>mdi-help</v-icon>
                                </v-card-subtitle>
                            </v-col>
                            <v-col 
                                cols="6"
                                sm="4"
                            >
                                <v-btn 
                                    @click="backRoute"
                                    small
                                    block
                                >
                                    Назад
                                    <v-icon>mdi-restart</v-icon>
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-container>
                    <v-divider />
                    <v-card-actions class="flex-grow-1 pa-0 flex-column">
                        <v-card-subtitle class="pb-0 text-center text-h6 w-full">
                            Можете посмотреть другие книги 
                        </v-card-subtitle>
                        <v-carousel
                            v-model="modelOnSlideAllBooks"
                            delimiter-icon="mdi-minus"
                            height="200"
                            :cycle="false"
                            :continuous="false"
                            :show-arrows="false"
                        >
                            <v-carousel-item
                                v-for="(item, i) in books"
                                :key="item"
                            >
                                <v-hover v-slot="{ hover }">
                                    <v-card
                                        class="align-self-center"
                                        elevation="1"
                                        height="inherit"
                                    >
                                        <v-img
                                            :src="item.cover"
                                            height="150px"
                                            contain
                                        />
                                        <v-overlay
                                            :value="hover"
                                            :key="i"
                                            opacity=".6"
                                            color="info"
                                            absolute
                                        >
                                            <v-container 
                                                fluid
                                                class="pa-0"
                                            >
                                                <v-row dense no-gutters>
                                                    <v-col cols="12">
                                                        <v-card-title>{{ item.title }}</v-card-title>
                                                        <v-card-subtitle>{{ item.author }}</v-card-subtitle>
                                                    </v-col>
                                                    <v-col cols="12">
                                                        <div class="d-flex px-3">
                                                            <v-rating 
                                                                half-increments
                                                                dense
                                                                color="secondary"
                                                                class="flex-grow-1 align-self-center"
                                                                :value="item.rating"
                                                            />
                                                            <v-btn 
                                                                color="success"
                                                                small
                                                            >
                                                                {{item.price}}
                                                                <v-icon 
                                                                    right
                                                                >
                                                                    mdi-currency-rub
                                                                </v-icon>
                                                            </v-btn>
                                                        </div>
                                                    </v-col>
                                                </v-row>
                                                <v-row dense>
                                                    <v-col 
                                                        cols="12"
                                                        class="d-flex justify-center"
                                                    >
                                                        <v-btn
                                                            :to="`/books/${i}`"
                                                            small
                                                        >
                                                            Посмотреть
                                                        </v-btn>
                                                    </v-col>
                                                </v-row>   
                                            </v-container> 
                                        </v-overlay>
                                    </v-card>
                                </v-hover>
                            </v-carousel-item>
                        </v-carousel>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
    import { mapState } from 'vuex';
    export default {
        name: "Books",
        data: () => ({
            isOpenDescriptionCard: false,
            isOpenMainDesctiption: true,
            isOpenReviews: true,
            modelOnSlideAllBooks: null,
            testReviews: [
            { 
                name: "test", 
                text: 
                `
                    Предварительные выводы неутешительны: 
                    постоянный количественный рост и сфера 
                    нашей активности не даёт нам иного выбора, 
                    кроме определения первоочередных требований.
                `,
                avatar: "account"
            }, 
            { divider: true, },
            {
                name: "test2", 
                text: 
                `
                    Также как дальнейшее развитие различных форм деятельности 
                    требует анализа приоретизации разума над эмоциями.
                `,
                avatar: "user"
            }
        ],
        }),
        methods: {
            backRoute() {
                return history.back();
            },
        },
        computed: {
            ...mapState({
                books: state => state.books.books.item,
            }),
            booksItem() {
                return this.$store.state.books.books.item[this.$route.params.id];
            },
            checkCurentCopy() {
                const onlineCopy = this.$store.state.books.books.item[this.$route.params.id].copy.online;
                const physicalCopy = this.$store.state.books.books.item[this.$route.params.id].copy.physical;


                if(onlineCopy && !physicalCopy) {
                    return ["Только онлаин копия"];
                }
                else if (physicalCopy && !onlineCopy) {
                    return ["Только физическая копия"]
                }
                else {
                    return ["Физическая копия", "Онлаин копия"]
                }
            }, 
        },
    }
</script>
<style scoped>
    .v-overlay__content {
        flex: 1 !important;
    }
</style>